<template>
	<div>
		<vue-headful title="Login â€“ FESTIVAL Pack'd" />
		<div class="container">
			<div class="row justify-content-around">
				<div class="col-md-4">
					<nav>
						<div class="nav-logo pb-0">
							<div class="nav-link">
								<img src="img/packr-icon.svg">
								<span class="ml-2">FESTIVAL Pack'd</span>
							</div>
						</div>
					</nav>
					<hr>
					<form name="loginForm" novalidate>
						<div class="form-group">
							<label>Username or E-Mail</label>
							<input type="text" class="form-control" :class="{'invalid': authData && authData.status == 404}" placeholder="John Appleseed" v-model="user.username" @input="$v.user.username.$touch()" @keyup="keyUp">
							
							<span class="text-danger" v-if="authData && authData.status == 404">{{ authData.message }}</span>
						</div>
						<div class="form-group">
							<label for="login-password">Password</label>
							<input type="password" class="form-control" :class="{'invalid': authData && authData.status == 401}" placeholder="Required" v-model="user.password" @input="$v.user.password.$touch()" @keyup="keyUp">
							
							<span class="text-danger" v-if="authData && authData.status == 401">{{ authData.message }}</span>
						</div>
					</form>
					
					<div class="row mt-3 d-none d-md-flex">
						<div class="col col-auto">
							<button class="btn btn-primary" @click="login()" :disabled="$v.user.$invalid || isWorking">Sign In</button>
						</div>
						
						<div class="col text-right" v-show="!isWorking">
							<router-link tag="button" class="btn btn-link mt-2 p-0" to="/register">Don't have an account yet?</router-link>
						</div>
						<div class="col text-right" v-show="isWorking">
							<div class="loading-indicator" />
						</div>
					</div>
					
					<div class="row mt-3 d-md-none">
						<div class="col col-12" v-show="!isWorking">
							<button class="btn btn-block btn-primary" @click="login()" :disabled="$v.user.$invalid || isWorking">Sign In</button>
						</div>
						
						<div class="col col-12 mt-3 text-center" v-show="!isWorking">
							<router-link tag="button" class="btn btn-link mt-2 p-0" to="/register">Don't have an account yet?</router-link>
						</div>
						<div class="col col-12 text-center" v-show="isWorking">
							<div class="loading-indicator" />
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<style lang="less" scoped>
.loading-indicator {
	margin-top: 7px;
}
</style>

<script>
import crypto from 'crypto-js';
import { AuthAPI } from "@/scripts/ApiUtil";
import { required } from 'vuelidate/lib/validators'

export default {
	name: 'login',
	data: () => {
		return {
			user: {
				username: "",
				password: ""
			},
			
			isWorking: false,
			authData: null
		}
	},
	validations: {
		user: {
			username: { required },
			password: { required }
		}
	},
	async created() {
		if (this.$cookies.get("authToken")) {
			if (this.$route.query.next) {
				this.$router.replace(this.$route.query.next);
			} else {
				this.$router.replace("/");
			}
		}
	},
	mounted() {
		window.scrollTo(0,0);
	},
	methods: {
		keyUp(e) {
			if (e.keyCode == 13 && !this.$v.user.$invalid) {
				this.login();
			}
		},
		async login() {
			this.isWorking = true;
			let _authData = await AuthAPI.login({
				username: this.user.username,
				password: crypto.SHA256(this.user.password).toString(crypto.enc.Hex)
			});
			
			if (_authData.status == 200) {
				// For some reason, we need this while we are in development mode
				this.$cookies.set("authToken", _authData.data["token"], (() => {
					const expiryDate = new Date();
					expiryDate.setHours(expiryDate.getHours() + 2);
					return expiryDate;
				})());
				
				if (this.$route.query.next) {
					this.$router.replace(this.$route.query.next);
				} else {
					this.$router.replace("/");
				}
			} else {
				this.authData = {
					status: _authData.status,
					message: _authData.data.message
				}
				this.isWorking = false;
				
				if (_authData.status == 500) {
					bootbox.alert(`There was an error signing you in:<br>${_authData.data.message}`);
				}
			}
		}
	}
}
</script>
